import React from "react"
import { Popover, Progress } from "antd"
import moment from "moment"
import "./index.scss"

interface ClockState {
  date: String
}

class Clock extends React.Component<{}, ClockState> {
  timeInterval: number
  //  this.state
  //  props super() 
  constructor(props) {
    super(props)
    this.state = {
      date: moment().format("YYYY-MM-DD H:mm:ss A"),
    }
  }

  componentDidMount() {
    // 
    this.timeInterval = window.setInterval(() => {
      this.setState({
        date: moment().format("YYYY-MM-DD HH:mm:ss A"),
      })
    }, 1000)
  }

  componentWillUnmount() {
    // 
    clearInterval(this.timeInterval)
  }

  timeFlow = () => {
    const endDate = moment().endOf("year").format("MM-DD, YYYY")
    const days = moment().dayOfYear()
    const isLeap = moment().isLeapYear()
    let fullDays = 365

    if (isLeap) {
      fullDays = 366
    }

    let leftDays = fullDays - days
    let percent = Number(((days * 100) / fullDays).toFixed(0))

    return (
      <div className="dateBox">
        <p className="title-bold">{endDate}</p>
        <p>{leftDays} days left</p>
        <Progress
          className="date-progress"
          type="line"
          percent={percent}
          status="active"
          strokeColor={{
            from: "#eee",
            to: "#777",
          }}
        ></Progress>
      </div>
    )
  }

  render() {
    return (
      <div className="clockBox">
        <Popover trigger="hover" title="Time Flow" content={this.timeFlow()}>
          <span className="clock-btn">{this.state.date}</span>
        </Popover>
      </div>
    )
  }
}

export default Clock
