import Axios from 'axios'
import { message } from 'antd'

//  Map ****
const pending = new Map()

// axios 
const service = Axios.create({
  baseURL: '/grow/',
  timeout: 60000,
  headers: { 'X-Requested-With': 'XMLHttpRequest' },
})

//  pending 
const addPending = (config) => {
  const url = [
    config.method,
    config.url,
    JSON.stringify(config.params),
    JSON.stringify(config.data),
  ].join('&')
  // current request config params
  //  canceltoken
  config.cancelToken =
    config.cancelToken ||
    new Axios.CancelToken((cancel) => {
      if (!pending.has(url)) {
        //  pending 
        pending.set(url, cancel)
      }
    })
}

/**
 *  pending 
 * 
 */
const removePending = (config) => {
  const url = [
    config.method,
    config.url,
    JSON.stringify(config.params),
    JSON.stringify(config.data),
  ].join('&')
  if (pending.has(url)) {
    //  pending 
    const cancel = pending.get(url)
    cancel(url)
    pending.delete(url)
  }
}

// 
export const clearPending = () => {
  for (const [url, cancel] of pending) {
    cancel(url)
  }
  pending.clear()
}

// 
service.interceptors.request.use(
  (config) => {
    removePending(config) // 
    addPending(config) //  pending 
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  (response) => {
    removePending(response.config) // 
    return response
  },
  (error) => {
    //  cancel 
    if (Axios.isCancel(error)) {
      //  cancel
      console.error(error.message)
    }
    return Promise.reject(error)
  }
)

/**
 * config.params 
 * config: { params: {}}  header 
 * config: { data: {}}  request body 
 */
export const get = async (url, config = {}) => {
  try {
    const res = await service.get(url, config)
    // 
    return res
  } catch (error) {
    message.error(String(error))
  }
}

/**
 * data  request body 
 */
export const post =  async (url, data, config = {}) => {
  try {
    const res = await service.post(url, data, config)
    // 
    return res
  } catch (error) {
    message.error(String(error))
  }
}


export const del = async (url, config = {}) => {
  try {
    const res = await service.delete(url, config)
    return res
  } catch (error) {
    message.error(String(error))
  }
}

export const patch = async (url, data, config = {}) => {
  try {
    const res = await service.patch(url, data, config)
    return res
  } catch (error) {
    message.error(String(error))
  }
}
